.home {
  .container {
    .description {
      opacity: 0;
      position: relative;
      height: 0;
      z-index: 10;
      display: flex;
      flex-direction: column;
      align-items: center;
      transition: all 500ms;
      -webkit-transition: all 500ms;
      top: 222px;
      svg {
        min-width: 30px;
        min-height: 30px;
        transform: scale(2);
        -webkit-text-shadow: 0px 0px 5px #000;
        -moz-text-shadow: 0px 0px 5px #000;
        text-shadow: 0px 0px 5px #000;
      }
      h3 {
        color: #fff;
        font-family: "Roboto", sans-serif;
        letter-spacing: 10px;
        font-size: 40px;
        -webkit-text-shadow: 0px 0px 5px #000;
        -moz-text-shadow: 0px 0px 5px #000;
        text-shadow: 0px 0px 5px #000;
      }
      p {
        color: #fff;
        font-family: "Roboto", sans-serif;
        letter-spacing: 10px;
        font-size: 40px;
        -webkit-text-shadow: 0px 0px 5px #000;
        -moz-text-shadow: 0px 0px 5px #000;
        text-shadow: 0px 0px 5px #000;
      }

      &.show {
        opacity: 1;
      }
    }
    .campanha {
      margin-bottom: -4px;
      overflow: hidden;
      height: 600px;
      img {
        transition: all 500ms;
        -webkit-transition: all 500ms;
        transform: scale(1);
        width: 100%;
        // z-index: -1;
        filter: grayscale(1);
        &.zoom {
          transform: scale(1.1);
          filter: grayscale(0);
        }
      }
    }
  }
}
