header.header{width:100%;max-width:1120px;height:100vh;margin:0 auto;display:flex;flex-direction:row;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;justify-content:center;padding:40px 0}header.header .main{display:flex;flex-wrap:wrap;flex-direction:column}header.header .main h1{margin:0;padding:0;outline:0}header.header .main h1.hi{font-family:"Roboto", sans-serif;color:#fff;letter-spacing:10px;font-size:16px;width:100%;margin-bottom:8px}header.header .main h1.first-name{font-family:"Druk Wide Web Super";-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#fff;font-size:77px;color:#000;letter-spacing:10px;width:-webkit-min-content;width:-moz-min-content;width:min-content;transform:scale(1, 1.3);margin:14px 0}header.header .main h1.last-name{font-family:"Druk Wide Web Super";letter-spacing:10px;font-size:77px;color:#fff;width:-webkit-min-content;width:-moz-min-content;width:min-content;transform:scale(1, 1.3)}header.header .main .break{display:flex}header.header .main .icon{display:flex}header.header .main .icon svg{position:relative;top:35px;left:-20px;transform:scale(3)}header.header .main .icon svg.firefox{left:-21px;top:37px}header.header .main h2.description{font-family:"Roboto", sans-serif;color:#fff;letter-spacing:9px;font-size:17px;width:-webkit-max-content;width:-moz-max-content;width:max-content;margin-top:-13px;margin-left:8px}header.header .about{transform:rotate(-90deg);display:flex;justify-content:center;align-items:flex-end}header.header .about a{font-family:"Roboto", sans-serif;text-decoration:none;color:#fff;letter-spacing:10px;font-size:17px;width:-webkit-max-content;width:-moz-max-content;width:max-content}

.home .container .description{opacity:0;position:relative;height:0;z-index:10;display:flex;flex-direction:column;align-items:center;transition:all 500ms;-webkit-transition:all 500ms;top:222px}.home .container .description svg{min-width:30px;min-height:30px;transform:scale(2);-webkit-text-shadow:0px 0px 5px #000;-moz-text-shadow:0px 0px 5px #000;text-shadow:0px 0px 5px #000}.home .container .description h3{color:#fff;font-family:"Roboto", sans-serif;letter-spacing:10px;font-size:40px;-webkit-text-shadow:0px 0px 5px #000;-moz-text-shadow:0px 0px 5px #000;text-shadow:0px 0px 5px #000}.home .container .description p{color:#fff;font-family:"Roboto", sans-serif;letter-spacing:10px;font-size:40px;-webkit-text-shadow:0px 0px 5px #000;-moz-text-shadow:0px 0px 5px #000;text-shadow:0px 0px 5px #000}.home .container .description.show{opacity:1}.home .container .campanha{margin-bottom:-4px;overflow:hidden;height:600px}.home .container .campanha img{transition:all 500ms;-webkit-transition:all 500ms;transform:scale(1);width:100%;-webkit-filter:grayscale(1);filter:grayscale(1)}.home .container .campanha img.zoom{transform:scale(1.1);-webkit-filter:grayscale(0);filter:grayscale(0)}

